<template>
    <div>
      <form>
        <v-text-field 
          v-model="cep"
          name="cep"
          id="cep"
          v-mask="'#####-###'"
          placeholder="Digite seu cep"
          @keyup="preencherCep"
        />
        <v-text-field
          v-model="rua"
          name="rua"
          id="rua"
          placeholder="Rua"
        />
      </form>
    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        name: 'ViaCep',
      data () {
        return {
          cep: '',
          rua: '',
          response: null,
        }
      },
      methods: {
        getCep(cep) {
          return axios.get(`https://viacep.com.br/ws/${cep}/json/`);
        },
        preencherCep() {
          const cep = this.cep.replace(/\D/g, "");
          if(cep.length === 8) {
            this.getCep(cep).then(response => {
              console.log(response);
              this.rua = response.data.logradouro;
            })
          }
        }
      },
    }
</script>
